import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';

@Component({
  selector: 'app-shoplivedialog',
  templateUrl: './shoplivedialog.component.html',
  styleUrls: ['./shoplivedialog.component.css'],
})
export class ShoplivedialogComponent implements OnInit {
  constructor(
    private _matDialog: MatDialogRef<ShoplivedialogComponent>,
    private router: Router
  ) {}

  ngOnInit(): void {}
  gotoSellerPage() {
    this._matDialog.close();
    this.router.navigate(['fblive']);
  }
}
